<template> 
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button
          style="float: right"
          @click="handleSearchList()"
          type="primary"
          size="small">
          查询结果
        </el-button>
        <el-button
          style="float: right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 25px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="供货商：">
            <el-select v-model="listQuery.firm" placeholder="请选择供货商" clearable>
              <el-option
                v-for="item in firmOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="货品名称：">
            <el-input style="" v-model="listQuery.stock" placeholder="货品名称"></el-input>
          </el-form-item>
          <el-form-item label="规格型号：">
            <el-input style="" v-model="listQuery.model" placeholder="规格型号"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
      <el-button
        style="margin-left: 20px;"
        class="btn-add"
        @click="handleAddStock()"
        size="mini">
        添加
      </el-button>
    </el-card>
    <div class="table-container">
      <el-table ref="stockTable"
                :data="list"
                style="width: 100%"
                v-loading="listLoading"
                border>
        <el-table-column label="供货商" width="100" align="center">
          <template slot-scope="scope">{{scope.row.firm}}</template>
        </el-table-column>
        <el-table-column label="货品名称" width="120" align="center">
          <template slot-scope="scope">{{scope.row.stock}}</template>
        </el-table-column>
        <el-table-column label="规格型号" align="center">
          <template slot-scope="scope">{{scope.row.model}}</template>
        </el-table-column>
        <el-table-column label="单位" width="120" align="center">
          <template slot-scope="scope">{{scope.row.unit}}</template>
        </el-table-column>
        <el-table-column label="数量" width="140" align="center">
          <template slot-scope="scope">{{scope.row.num}}</template>
        </el-table-column>
        <el-table-column label="单价" width="100" align="center">
          <template slot-scope="scope">{{scope.row.price}}</template>
        </el-table-column>
        <el-table-column label="总价" width="100" align="center">
          <template slot-scope="scope">{{getTotalPrice(scope.row.num, scope.row.price)}}</template>
        </el-table-column>
        <el-table-column label="备注" width="100" align="center">
          <template slot-scope="scope">{{scope.row.other}}</template>
        </el-table-column>
        <el-table-column label="操作" width="160" align="center">
          <template slot-scope="scope">

              <el-button
                size="mini"
                @click="handleUpdateStock(scope.$index, scope.row)">编辑
              </el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDeleteStock(scope.$index, scope.row)">删除
              </el-button>

          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="pagination-container">
      <el-pagination

        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :current-page.sync="listQuery.pageNum"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title="编辑货品信息"
      :visible.sync="editInfo.dialogVisible"
      :top = "'10vh'"
      width="60%">
      <el-form :model="editInfo.stock" :rules="rules" ref="stockInfoForm"  label-width="15%" style="width: 90%;margin: auto" size="small">
        <el-form-item label="供货商：" prop="firm">
          <el-input v-model="editInfo.stock.firm"></el-input>
        </el-form-item>
        <el-form-item label="货品名称：" prop="stock">
          <el-input v-model="editInfo.stock.stock"></el-input>
        </el-form-item>
        <el-form-item label="规格型号：" prop="model">
          <el-input v-model="editInfo.stock.model"></el-input>
        </el-form-item>
        <el-form-item label="单位：" prop="unit">
          <el-input v-model="editInfo.stock.unit"></el-input>
        </el-form-item>
        <el-form-item label="数量：" prop="num">
          <el-input-number v-model="editInfo.stock.num"></el-input-number>
        </el-form-item>
        <el-form-item label="单价：" prop="price">
          <el-input-number v-model="editInfo.stock.price"></el-input-number>
        </el-form-item>
        <el-form-item label="备注：" prop="other">
          <el-input v-model="editInfo.stock.other"
                    type="textarea"
                    :rows="5"></el-input>
        </el-form-item>
        <el-form-item style="text-align: center">
          <el-button type="primary" size="medium" @click="handleEditStock('stockInfoForm')">确 定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
  import {
    fetchStockList,
    fetchFirmList,
    updateStock, delStock
  } from '@/api/stock'

  const defaultListQuery = {
    pageNum: 1,
    pageSize: 5,
    firm: null,
    stock: null,
    model: null
  };
  export default {
    name: "sotckList",
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        list: null,
        total: null,
        listLoading: true,

        firmOptions: [],
        editInfo:{
          dialogVisible:false,
          stock : {}
        },
        rules: {
          firm:  [{required: true, message: '请输入', trigger: 'blur'}],
          stock: [{required: true, message: '请输入', trigger: 'blur'}],
          model: [{required: true, message: '请输入', trigger: 'blur'}],
          unit: [{required: true, message: '请输入', trigger: 'blur'}],
          num: [{required: true, message: '请输入', trigger: 'blur'}],
          price: [{required: true, message: '请输入', trigger: 'blur'}],
          other: [{required: false, message: '请输入', trigger: 'blur'}]
        }
      }
    },
    created() {
      this.getStockList();
      this.getFirmList();
    },
    watch: {

    },
    filters: {

    },
    methods: {
      handleSearchList() {
        this.listQuery.pageNum = 1;
        // this.getList();
        this.getStockList();
      },
      handleSizeChange(val) {
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getStockList();
      },
      handleCurrentChange(val) {
        this.listQuery.pageNum = val;
        this.getStockList();
      },
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      getStockList() {
        this.listLoading = true;
        fetchStockList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        });
      },
      getTotalPrice(num, price){
        let total = num * price;
        if(total < 0)
          total = 0;
        return total;
      },
      getFirmList() {
        fetchFirmList().then(response => {
          this.firmOptions = [];
          let firmList = response.data.list;
          console.log(firmList)
          for (let i = 0; i < firmList.length; i++) {
            this.firmOptions.push({label: firmList[i].firm, value: firmList[i].firm});
          }
        });
      },
      handleUpdateStock(index,row){
        this.$nextTick(()=>{
          if (this.$refs['stockInfoForm'] != null)
            this.$refs['stockInfoForm'].resetFields();
        })
        this.editInfo.stock = Object.assign({}, row);
        this.editInfo.dialogVisible=true;
      },
      handleDeleteStock(index, row){
        this.$confirm('是否要进行删除操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delStock(row.stockId).then(response => {
            this.$message({
              message: '删除成功',
              type: 'success',
              duration: 1000
            });
            this.getStockList();
            this.getFirmList();
          });
        });
      },
      handleEditStock(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('是否要进行修改', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              updateStock(this.editInfo.stock.stockId, this.editInfo.stock).then(response => {
                this.$message({
                  message: '修改成功',
                  type: 'success',
                  duration: 1000
                });
                this.editInfo.dialogVisible = false;
                this.getStockList();
                this.getFirmList();
              });
            });
          }
        });
      },
      handleAddStock() {
        this.$router.push({path:'/stock/cStock'});
      }

    }
  }
</script>
<style></style>





